"use client";

import useAppLayoutHook from "@/shared/ui/hooks/app_layout.hook";
import { useEffect } from "react";
import { motion } from "framer-motion";
import { Plus } from "lucide-react";
import { Delete, EditIcon } from "@/assets/svg/svg_icon";

interface DeliveryAddress {
    id: number;
    name: string;
    address: string;
    phone: string;
}

const ProfilePage = () => {
    const { handleProtectedRoutes } = useAppLayoutHook();

    useEffect(() => {
        handleProtectedRoutes();
    });

    const deliveryAddresses: DeliveryAddress[] = [
        {
            id: 1,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
        {
            id: 2,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
        {
            id: 3,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
    ];

    return (
        <div className="">
            {/* Profile Header */}
            <div className="bg-white rounded-3xl p-6 mb-6 shadow-sm">
                <div className="flex justify-between items-center mb-6">
                    <h2 className="text-2xl font-medium">My Profile</h2>
                    <button
                        className="group w-40 p-0.5 border border-[#FDA106] backdrop-blur-[10px] rounded-[18px] cursor-pointer"
                    >
                        <div className="relative w-full rounded-[14px] overflow-hidden bg-gradient">
                            {/* Hover Overlay */}
                            <div className="absolute inset-0 bg-[#FFF4EA] opacity-0 transition-opacity duration-300 group-hover:opacity-100 flex items-center justify-center">
                                <span className="text-[#FDA106] font-semibold">
                                    Edit Profile
                                </span>
                            </div>

                            {/* Default State */}
                            <div className="relative z-10 py-4 font-semibold flex items-center justify-center text-black transition-opacity duration-300 group-hover:opacity-0">
                                Edit Profile
                            </div>
                        </div>
                    </button>
                </div>

                <div className="flex items-start gap-6">
                    {/* Profile Image */}
                    <div className="w-24 h-24 bg-gray-300 rounded-full flex-shrink-0"></div>

                    {/* Profile Info */}
                    <div className="flex-1">
                        <div className="flex items-center gap-10">
                            <div>
                                <h3 className="font-semibold text-gray-900 mb-1">
                                    Otor John Stephen
                                </h3>
                                <p className="text-gray-600 mb-1">
                                    uluxotor
                                </p>
                                <p className="text-gray-600">
                                    +234 8130000000
                                </p>
                            </div>
                            <div className="w-[1.5px] h-16 bg-gray-200" />
                            <div className="max-w-md">
                                <h4 className="font-semibold text-gray-900 mb-1">
                                    Residential address
                                </h4>
                                <p className="text-gray-600">
                                    Sell new items or used items you no longer need or used items you no longer need
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <section className="bg-white rounded-[18px] p-4 shadow-sm flex justify-between items-center mb-5">
                <div>
                    <h3 className="font-semibold text-[20px]">
                        Delivery Address{" "}
                        <span className="text-gray-700">({deliveryAddresses.length})</span>
                    </h3>
                </div>

                <button
                    type="button"
                    className="group w-full max-w-[200px] p-0.5 border border-[#003625] rounded-[18px] cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed">
                    <div className="flex items-center gap-2 w-full bg-[#003625] text-white py-4 px-4 rounded-[14px] text-center font-semibold transition-colors duration-200 group-hover:bg-emerald-100 group-hover:text-[#052214]">
                        <Plus className="w-6 h-6" />
                        Add New Address
                    </div>
                </button>
            </section>


            {/* Delivery Address Section */}
            {/* Address List */}
            <div className="space-y-4 rounded-[18px]">
                {deliveryAddresses.map((address) => (
                    <div
                        key={address.id}
                        className="border border-gray-200 rounded-[18px]  bg-white p-5"
                    >
                        <h3 className="font-semibold text-gray-900 mb-2">
                            {address.name}
                        </h3>
                        <p className="text-sm text-gray-600 mb-1">
                            {address.address}
                        </p>
                        <p className="text-sm text-gray-600 mb-4">
                            {address.phone}
                        </p>

                        <div className="flex gap-3">
                            <button
                                className="group w-[150px] p-0.5 border border-[#FDA106] backdrop-blur-[10px] rounded-[18px] cursor-pointer"
                            >
                                <motion.div
                                    whileHover="hovered"
                                    className="relative w-full rounded-[14px] overflow-hidden bg-gradient"
                                >
                                    {/* Hover Overlay */}
                                    <div className="absolute inset-0 bg-[#FFF4EA] opacity-0 transition-opacity duration-300 group-hover:opacity-100 flex items-center justify-center">
                                        <div className="flex items-center gap-2 text-[#FDA106] font-semibold">
                                            <EditIcon stroke={"#FDA106"} />
                                            <motion.p
                                                variants={{
                                                    hovered: { x: -6 }
                                                }}
                                                transition={{ type: "spring", stiffness: 400, damping: 25 }}
                                                className="text-sm"
                                            >
                                                Edit Address
                                            </motion.p>
                                        </div>
                                    </div>

                                    {/* Default State */}
                                    <div className="flex items-center gap-2 relative z-10 py-2.5 font-semibold flex items-center justify-center text-black transition-opacity duration-300 group-hover:opacity-0">
                                        <EditIcon />
                                        <motion.span
                                            variants={{
                                                hovered: { x: -6 }
                                            }}
                                            transition={{ type: "spring", stiffness: 400, damping: 25 }}
                                            className="text-sm"
                                        >
                                            Edit Address
                                        </motion.span>
                                    </div>
                                </motion.div>
                            </button>

                            <button className="border border-red-500 text-[#D24545] cursor-pointer px-5 py-2 rounded-[18px] text-sm font-medium flex items-center gap-2 transition-colors">
                                <Delete />
                                Delete Address
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default ProfilePage;